<template>
  <section class="parse_div_wrap">
    <div class="row">
      <div class="col-4">
        <textarea v-model="input_code">
        </textarea>
      </div>
      <div class="col-4">
        <textarea v-html="tokens"></textarea>
      </div>
      <div class="col-4">
        <textarea v-html="parses"></textarea>
      </div>
    </div>
  </section>
</template>

<script>
  var esprima = require('esprima');
  export default {
    name: 'parse',
    data() {
      return {
        Keyword: ['break', 'case', 'catch', 'continue', 'default', 'else', 'finally', 'in', 'return', 'void',],
        Statement: ['delete', 'do', 'for', 'if', 'new', 'switch', 'this', 'throw', 'try', 'typeof', 'while', 'with', 'instanceof'],
        Declaration: ['var', 'function'],
        Punctuator: [
          '.', '!', '?', ':', ',', ';', '|', '||', '&', '&&', '=', '+', '++', '-', '--', '==', '*', '/', '%', '>', '<', '>=', '<=',
          '+=', '-=', '*=', '/=', '(', ')', '{', '}'
        ],
        Identifier: [],
        //______________________________________________________________________________________________________________
        input_code: 'var a = b + c ;',
        tokens: [],
        parses: []
      }
    },
    methods: {
     /* tokenizeCode(code) {
        let tokens = [];  // 保存结果数组

        this.Punctuator.forEach(function (char_str) {
          if (char_str === '++') {
            code = code.replace(/(?<=[^\+\+])\+\+(?=[^\+\+])/igm, '\t' + char_str + '\t')
          } else if (char_str === '--') {
            code = code.replace(/(?<=[^\-\-])\-\-(?=[^\-\-])/igm, '\t' + char_str + '\t')
          } else if (char_str === '==') {
            code = code.replace(/(?<=[^\=\=])\=\=(?=[^\=\=])/igm, '\t' + char_str + '\t')
          } else if (char_str === '&&') {
            code = code.replace(/(?<=[^\&\&])\&\&(?=[^\&\&])/igm, '\t' + char_str + '\t')
          } else if (char_str === '||') {
            code = code.replace(/(?<=[^\|\|])\|\|(?=[^\|\|])/igm, '\t' + char_str + '\t')
          } else {
            let rex = new RegExp(`(?<=[^\\` + char_str + `])` + '\\' + char_str + `(?=[^\\` + char_str + `])`, 'igm');
            code = code.replace(rex, '\t' + char_str + '\t')
          }
        })

        for (let i = 0; i < code.length; i++) { // 遍历代码源字符串
          let currentChar = code.charAt(i);  // 从0开始 一个个字符读取 --当前读到的字符

          // 连续的空白字符组合在一起
          if (!(/\s/.test(currentChar))) { //如果不是空白字符
            let code_str = currentChar

            // 继续遍历下一个字符
            for (i++; i < code.length; i++) {
              currentChar = code.charAt(i);
              if (!/\s/.test(currentChar)) {
                code_str += currentChar; // 连接字符
              } else {
                //去掉空格后得到的连接字符
                i--;
                //______________________________________________________________________________________________________


                //关键字处理
                if (this.Keyword.indexOf(code_str) > -1) { //如果读到关键字
                  let token = { //格式化一个代表字符串的标志
                    type: 'Keyword',
                    value: code_str
                  };
                  tokens.push(token); // 则添加一个字符串标志
                  break;
                }

                //表达式处理
                if (this.Statement.indexOf(code_str) > -1) { //如果读到关键字
                  let token = { //格式化一个代表字符串的标志
                    type: 'Statement',
                    value: code_str
                  };
                  tokens.push(token); // 则添加一个字符串标志
                  break;
                }

                //声明处理
                if (this.Declaration.indexOf(code_str) > -1) { //如果读到关键字
                  let token = { //格式化一个代表字符串的标志
                    type: 'Declaration',
                    value: code_str
                  };
                  tokens.push(token); // 则添加一个字符串标志
                  break;
                }

                //标点符号处理
                if (this.Punctuator.indexOf(code_str) > -1) { //如果读到标点符号
                  let token = { //格式化一个代表字符串的标志
                    type: 'Punctuator',
                    value: code_str
                  };
                  tokens.push(token); // 则添加一个字符串标志
                  break;
                }

                // 数字做处理
                if (/^[0-9]/.test(code_str)) {   // 如果数字是以 0 到 9的字符开始的话
                  let token = {
                    type: 'Number',
                    value: code_str
                  };
                  tokens.push(token);
                  break;
                }
                //字符串处理
                if (/^[\"|\'][0-9]*!/.test(code_str)) {   // 如果数字是以 0 到 9的字符开始的话
                  let token = {
                    type: 'String',
                    value: code_str
                  };
                  tokens.push(token);
                  break;
                }
                // 标识符(字母，$, _开始的) 处理
                if ((/[a-zA-Z\$\_]/.test(code_str)) && (this.Keyword.indexOf(code_str) === -1)) {
                  let token = {
                    type: 'Identifier',
                    value: code_str
                  };
                  tokens.push(token);
                  break;
                }


                //______________________________________________________________________________________________________
                // 更多的字符判断 ......
                // 遇到无法理解的字符 直接抛出异常
                throw new Error('Unexpected ' + code_str);
              }
            }
          }
        }

        return tokens;
      },
      parse(tokens) {
        let i = 0;     // 用于标识当前遍历位置
        let curToken;   // 用于记录当前符号

        const ast = {
          type: 'Program',
          body: [],
          sourceType: "script"
        };
        let body = ast.body;

        function nextToken(index) {
          if (index) {
            i += index
          } else {
            i++
          }
        }

        function getAst(curToken) {

          switch (curToken.type) {

            case 'Punctuator':

              if (curToken.value === '(') {
                if (tokens[i - 1].type === 'Identifier' && tokens[i - 2].value === 'function') {

                  let Declaration = {
                    "type": "FunctionDeclaration",
                    "id": {
                      "type": "Identifier",
                      "name": tokens[i - 1].value
                    },
                    "params": [],
                    "body": {
                      "type": "BlockStatement",
                      "body": [],
                      'tokens': []
                    },
                  }

                  while (tokens[i + 1].type === 'Identifier' && (tokens[i + 2].value === ',' || tokens[i + 2].value === ')')) {

                    Declaration.params.push({
                      "type": "Identifier",
                      "name": tokens[i + 1].value
                    })

                    nextToken(2)
                  }

                  while (tokens[i].value !== '{') {

                  }

                  body.push(Declaration)
                }
              }

              break;
          }

        }

        // 逐条解析顶层语句
        while (i < tokens.length) {
          curToken = tokens[i]

          getAst(curToken)

          i++
        }
        return ast;
      }*/
    },
    watch: {
      input_code: function (vale) {
        try {
          this.tokens = esprima.tokenize(vale);
        } catch (e) {
          this.tokens = e;
        }
        try {
          this.parses = esprima.parse(vale);
        } catch (e) {
          this.parses = e;
        }
      }
    },
    mounted() {
      try {
        this.tokens = esprima.tokenize(this.input_code);
      } catch (e) {
        this.tokens = e;
      }
      try {
        this.parses = esprima.parse(this.input_code);
      } catch (e) {
        this.parses = e;
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .parse_div_wrap {
    width: 100%;
    height: 100%;
    color: wheat;
    border: thin solid wheat;
    border-radius: 10px;
    background-image: url("../../static/img/bj.png");
    .row {
      height: 100%;
      .col-4{
        height: 100%;
        input,textarea {
          width: 100%;
          height: 100%;
          padding: 1rem;
          border: thin solid wheat;
          color: wheat;
          border-radius: 10px;
          background: none;
        }
      }
    }
  }
</style>



